import React from "react";
import "./styles.css";
import Articles from "./articles";
import AuthorLogo from "./components/AuthorLogo";
import AuthorName from "./components/AuthorName";
import Title from "./components/Title";
import Description from "./components/Description";
import MainImage from "./components/MainImage";
function App() {
  const BookmarkButton = () => (
    <img src="./images/bookmark-regular.svg" className="icon" />
  );
  const MoreOptionsButton = () => (
    <img src="./images/ellipsis-h-solid.svg" className="icon" />
  );

  return (
    <div>
      {Articles.map((article) => (
        <main className="article">
          <div className="content">
            <AuthorLogo source={article.authorImage} />
            <AuthorName
              author={article.author}
              category={article.category.name}
            />
            <Title title={article.title} />
            <Description description={article.description} />
            <div className="buttons">
              <BookmarkButton />
              <MoreOptionsButton />
            </div>
          </div>
          <MainImage image={article.image.url} alt={article.image.alt} />
        </main>
      ))}
    </div>
  );
}

export default App;
